'use client';

import { useEffect, useState } from 'react';
import axios from '@/lib/axios'; 

export default function Home() {
  const [status, setStatus] = useState<string>('Menghubungkan ke Laravel...');
  const [error, setError] = useState<string | null>(null);
  const [backendUrl, setBackendUrl] = useState<string>('');

  useEffect(() => {

    setBackendUrl(process.env.NEXT_PUBLIC_BACKEND_URL || 'URL tidak ditemukan di .env.local');

    const checkConnection = async () => {
      try {

        await axios.get('/sanctum/csrf-cookie');
        

        setStatus('✅ Berhasil Terhubung ke MainDiHatta API!');
        
      } catch (err: any) {
        console.error('Connection Error:', err);
        setStatus('❌ Gagal Terhubung');
        
        const errorMessage = err.response?.data?.message || err.message || 'Unknown error occurred';
        setError(errorMessage);
      }
    };

    checkConnection();
  }, []);

  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-6 md:p-24 bg-gray-50">
      <div className="bg-white p-8 rounded-xl shadow-lg text-center max-w-lg w-full border border-gray-100">
        <h1 className="text-2xl font-bold mb-6 text-gray-800">
          MainDiHatta.id <span className="text-blue-600 text-sm font-medium bg-blue-50 px-2 py-1 rounded ml-1">Frontend TS</span>
        </h1>
        
        {/* Status Box */}
        <div className={`p-4 rounded-lg transition-colors duration-300 ${error ? 'bg-red-50 border border-red-200' : 'bg-green-50 border border-green-200'}`}>
            <p className={`text-lg font-semibold ${error ? 'text-red-600' : 'text-green-700'}`}>
            {status}
            </p>
        </div>

        {/* Error Details Box */}
        {error && (
          <div className="mt-6 text-left">
            <div className="p-4 bg-gray-100 text-gray-700 text-xs rounded-md overflow-x-auto border border-gray-300 font-mono">
              <p className="font-bold mb-2 text-gray-900 border-b border-gray-300 pb-1">Debug Info:</p>
              
              <div className="mb-2">
                <span className="font-semibold">Target URL:</span> <br/>
                <span className="text-blue-600 break-all">{backendUrl}</span>
              </div>
              
              <div>
                <span className="font-semibold">Error Message:</span> <br/>
                <span className="text-red-500">{error}</span>
              </div>
            </div>
            
            <p className="mt-3 text-gray-500 text-xs text-center italic">
              *Pastikan Laravel berjalan (php artisan serve) dan konfigurasi CORS/Sanctum sudah benar.
            </p>
          </div>
        )}
      </div>
    </main>
  );
}